<template>
  <div class="product-list-page py-5">
    <div class="container">
      <div class="row mb-4">
        <div class="col-12">
          <h1 class="display-4">{{ pageTitle }}</h1>
          <div class="d-flex justify-content-between align-items-center">
            <p class="lead mb-0">{{ pageDescription }}</p>
            <div class="category-filter">
              <select v-model="selectedCategory" class="form-select" @change="updateCategoryFilter">
                <option value="">All Categories</option>
                <option v-for="category in categories" :key="category.name" :value="category.name">
                  {{ category.name }}
                </option>
              </select>
            </div>
          </div>
        </div>
      </div>
      
      <ProductsSection 
        :products="filteredProducts"
        :selected-category="selectedCategory"
        @add-to-cart="addToCart"
      />
    </div>
  </div>
</template>

<script>
import { computed, ref, watch } from 'vue'
import { useStore } from 'vuex'
import { useRoute, useRouter } from 'vue-router'
import ProductsSection from '../components/ProductsSection.vue'

export default {
  components: {
    ProductsSection
  },
  props: {
    category: {
      type: String,
      default: null
    }
  },
  setup(props) {
    const store = useStore()
    const route = useRoute()
    const router = useRouter()
    
    const selectedCategory = ref(props.category || '')
    
    const products = computed(() => store.state.products)
    const categories = computed(() => store.state.categories)
    
    const pageTitle = computed(() => {
      return selectedCategory.value 
        ? `${selectedCategory.value} Products`
        : 'Our Products'
    })
    
    const pageDescription = computed(() => {
      return selectedCategory.value
        ? `Discover our premium ${selectedCategory.value.toLowerCase()} collection`
        : 'Discover our premium collection'
    })
    
    const filteredProducts = computed(() => {
      if (!selectedCategory.value) return products.value
      return products.value.filter(
        product => product.category === selectedCategory.value
      )
    })
    
    const updateCategoryFilter = () => {
      if (selectedCategory.value) {
        router.push({
          path: '/products',
          query: { category: selectedCategory.value }
        })
      } else {
        router.push('/products')
      }
    }
    
    const addToCart = (product) => {
      store.dispatch('addToCart', product)
    }
    
    // Watch for route changes to update selected category
    watch(() => route.query.category, (newCategory) => {
      selectedCategory.value = newCategory || ''
    })
    
    return {
      products,
      categories,
      filteredProducts,
      selectedCategory,
      pageTitle,
      pageDescription,
      addToCart,
      updateCategoryFilter
    }
  }
}
</script>

<style scoped>
.product-list-page {
  background-color: #f8f9fa;
  min-height: 80vh;
}

.category-filter {
  width: 250px;
}

@media (max-width: 768px) {
  .category-filter {
    width: 100%;
    margin-top: 1rem;
  }
  
  .d-flex {
    flex-direction: column;
    align-items: flex-start !important;
  }
}
</style>